@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans&display=swap);
* {
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: 'Raleway',-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --theme_color: #ffffff;
}


#comp-model {
  width: 100vw;
  height: 100vh;
}

#landing-main {
  position: absolute;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  z-index: 2;
  width: 100vw;
  height: 100vh;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  /* background-color: #333; */
  background: url(/drishti-3d/static/media/back.efa3603a.webp);
  background-repeat: no-repeat;
  background-size: cover;
  background-blend-mode: color;
  background-position: center;
}

#landing-head {
  width: 100%;
  font-family: 'Josefin Sans', sans-serif;
  font-size: 5em;
  color: #ffffff;
  color: var(--theme_color);
  text-align: center;
  margin: 5px;
  letter-spacing: -0.1em;
}

#landing-button {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  margin: 10px;
}

#landing-button button {
  margin: 15px;
  padding: 10px;
  width: 120px;
  border: none;
  font-family: 'Josefin Sans', sans-serif;
  color: #ffffff;
  color: var(--theme_color);
  border-radius: 50px;
  background: #000000;
}

#landing-instructions {
  background-color: rgba(255, 255, 255, 0.05);
  margin: 5px;
  width: 100%;
  text-align: center;
  padding: 5px 0px 5px 0px;
  color: #ffffff;
  color: var(--theme_color);
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
}

#landing-instructions div {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

#landing-instructions div p {
  margin: 2px;
  word-spacing: 0.2em;
  letter-spacing: -0.01em;
}

#landing-instructions div svg {
  margin: 2px;
  fill: #ffffff;
  fill: var(--theme_color);
}

#landing-button button:hover {
  border: 1px solid #57cbcc;
}

#landing-toggle {
  display: none;
  position: absolute;
  z-index: 5;
  padding: 10px;
  margin: 10px;
}

#landing-toggle svg{
  fill: white;
  width: 30px;
  height: 30px;
}

#desktop-esc {
  position: absolute;
  bottom: 0;
  margin: 2em;
  z-index: 10;
  text-align: center;
  display: none;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}

#desktop-esc div{
  position: relative;
  margin: auto;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

#desktop-esc img {
  width: 50px;
  height: 50px;
}

#desktop-esc span {
  font-size: smaller;
  color: #ffffff;
}

#mobile-controls {
  position: absolute;
  z-index: 1;
  right: 0;
  bottom: 0;
  margin: 1.5em;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

#mobile-controls button{
  height: 30px;
  width:  30px;
  margin: 0.5em;
  border: 2px solid rgb(255, 255, 255);
  border-radius: 6px;
  background: none;
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
}

#mobile-controls svg{
  fill: white;
  pointer-events: none;
}

#mobile-controls span{
  color: #ffffff;
  font-size: 1em;
  font-weight: bolder;
}

@media screen and (max-width: 600px) {
  #landing-head {
    font-size: 3.5em;
  }

  #landing-button {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;
    width: 100%;
  }

  #landing-button button {
    width: 200px;
  }
}

@media screen and (max-width: 400px) {
  #landing-head {
    font-size: 2.75em;
  }

  #landing-instructions div p {
    font-size: small;
  }

  #landing-instructions div svg {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }
}
